import React, {Component} from "react";
import logo from "./logo.svg";
import "./Home.css";
import {Jumbotron, Container} from "reactstrap";
import {Link} from "react-router-dom";
import Layout from "../Layout.js";
import ScrollableAnchor from "react-scrollable-anchor";
import Acknowledgments from "./Contributors";
import Learn from "./Learn";
import Publications from "./Publications";
import Research from "./Research";
import Projects from "./Projects";
import WhyStateChannels from "./Why-do-we-need-state-channels";


class GettingStarted extends Component {
  render() {
    return (
      <div>
        <Container class="Main-container" style={{textAlign: "left",
          height:'100%',
          paddingLeft:'75px'
        }}>
          <h1 style={{paddingTop: "20px",paddingBottom: "20px", textAlign: "left"}}>
            Welcome!
          </h1>

          <p
            style={{
              paddingBottom: "20px",
              textAlign: "left"
            }}
          >
            If you ended up here, you’re probably interested in learning about
            state channels, a method for enabling cheap, low-latency
            transactions on Ethereum.
            <br /> <br />
            We started LearnChannels because the existing information about
            state channels is spread all over the place. We wanted to gather all
            of that information in a single place and make it much easier for
            someone to browse. At the same time, we found that there simply
            isn’t very much easy-to-understand learning material about state
            channels. Inspired by LearnPlasma, our goal is to make state
            channels approachable for developers and laymen alike.
              </p>
        <p>  <strong>*Note:</strong> these pages assume basic familiarity with blockchain concepts such as smart contracts.
</p>
          <h2>Table of Contents</h2>
          <ul>
            <li>
              <a class="link" href='#WhyStateChannels'>
                Learn
              </a>
            </li>
            <li>
              <a class="link" href="#Research">
                Research
              </a>
            </li>
            <li>
              <a class="link" href="#Publications">
                Publications
              </a>
            </li>
            <li>
              <a class="link" href="#Projects">
                Projects
              </a>
            </li>
            <li>
              <a class="link" href="#Acknowledgments">
                Acknowledgments
              </a>
            </li>
          </ul>
        </Container>
      </div>
    );
  }
}
export default GettingStarted;



