import React, {Component} from "react";
import "./Home.css";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Container,
  Row,
  Col,
  Jumbotron,
  Button
} from "reactstrap";
import {Link} from "react-router-dom";
import {HamburgerButton} from "react-hamburger-button";


class Layout extends Component {
  constructor() {
    super();

    this.state = {
      showMenu: false,
      showMenu2: false,
      windowWidth: window.innerWidth,
      mobileNavVisible: false
    };

    this.showMenu = this.showMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
    this.showMenu2 = this.showMenu2.bind(this);
    this.closeMenu2 = this.closeMenu2.bind(this);
  }

  showMenu(event) {
    event.preventDefault();

    this.setState({ showMenu: true }, () => {
      document.addEventListener('click', this.closeMenu);
    });
  }
  showMenu2(event) {
    event.preventDefault();

    this.setState({ showMenu2: true }, () => {
      document.addEventListener('click', this.closeMenu2);
    });
  }

  closeMenu(event) {

    if (!this.dropdownMenu.contains(event.target)) {

      this.setState({ showMenu: false }, () => {
        document.removeEventListener('click', this.closeMenu);
      });

    }
  }
  closeMenu2(event) {

    if (!this.dropdownMenu2.contains(event.target)) {

      this.setState({ showMenu2: false }, () => {
        document.removeEventListener('click', this.closeMenu2);
      });

    }
  }
  handleResize() {
    this.setState({windowWidth: window.innerWidth});
  }
    
  componentDidMount() {
    window.addEventListener('resize', this.handleResize.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize.bind(this));
  } 
  navigationLinks()  {
    return[
      <div
      style={{
      height: "100%",
      fontFamily: "Verdana, Geneva, sans-serif",
      position: "relative"
      }}
    >
    <div class="topnav" style={{
      overflow:'visible',
      paddingLeft: '20px',
      marginBottom:'10px'
      }}
    >
    <div class="mainmenu">
      <a class="active" href="/">
        LearnChannels
      </a>
      <a class="link" href="#GettingStarted">
        Get Started
      </a>
      <div class="dropWrapper"
      style={{
      position: 'relative',
      }}
      >
        <a onClick={this.showMenu}>
          Learn
        </a>
        {this.state.showMenu
            ? (
            <div ref={(element) => {
                  this.dropdownMenu = element;
                }}
              class="link"
              style={{
                textAlign:'left',
                display:'inline-block',
                position: 'absolute',
                width: '100%',
                left:'0',
                top:'65px',
                backgroundColor: '#ddd',
                minWidth: '160px',
                boxShadow: '0px 8px 16px 0px rgba(0,0,0,0.2)',
                padding: '12px 16px',
                zIndex: '1000'
              }}>
            <a style={{
              textAlign:'left'}} class = "link" href='#WhyStateChannels'> Why do we need state channels?</a>
            <a style={{
              textAlign:'left'}} class = "link" href='#Statechannels101'>State channels 101</a>
            <a style={{
              textAlign:'left'}} class = "link" href='#Paymentchannels'>Payment channels vs. State channels</a>
            <a style={{
              textAlign:'left'}} class = "link" href='#Counterfactualinstantiation'>Counterfactual instantiation</a>
            <a style={{
              textAlign:'left'}} class = "link" href='#Htlcs'>Hashed timelock contracts</a>
            <a style={{
              textAlign:'left'}} class = "link" href='#Virtualchannels'>Virtual channels</a>
            </div>)
            : (
              null
            )
          }
        </div>
      <a class="link" href="#Research">
        Research
      </a>
      <a class="link" href="#Publications">
        Publications
      </a>
      <a class="link" href="#Projects">
        Projects
      </a>
      <a onClick={this.showMenu2}>
        Developers
      </a>
      <div class="dropWrapper"
      style={{
      position: 'relative',
      }}>
          {this.state.showMenu2
              ? (
              <div ref={(element) => {
                    this.dropdownMenu2 = element;
                  }}
                class= "link"
                style={{
                  textAlign:'left',
                  display:'inline-block',
                  position: 'absolute',
                  width: '100%',
                  left:'0',
                  top:'65px',
                  backgroundColor: '#ddd',
                  minWidth: '160px',
                  boxShadow: '0px 8px 16px 0px rgba(0,0,0,0.2)',
                  padding: '12px 16px',
                  zIndex: '1000'
                }}>
              <a style={{
                textAlign:'left'}} class = "link" href='#PaymentChannelContracts'> Payment Channel Contracts</a>
              </div>)
              : (
                null
              )
          }
          </div>
        </div>
      <a class="link" href="#Acknowledgments">
        Contributors
      </a>
  </div>
  </div>
    ]
  }
  renderMobileNav() {
    if(this.state.mobileNavVisible) {
      return [
      
      <div style={{
        display:"block",
        width: "100%",
        backgroundColor:"F5F5F5",
        top:"0"
            }}>
        {this.navigationLinks()}

      </div>
      ]
    }
  }
  
  handleNavClick() {
    if(!this.state.mobileNavVisible) {
      this.setState({mobileNavVisible: true});
    } else {
      this.setState({mobileNavVisible: false});
    }
  }
  renderNavigation() {
    if(this.state.windowWidth <= 1080) {
      return [
        <Container style={{
          paddingBottom:"1.5%",
          backgroundColor:"F5F5F5"
        }}>
          <Row>
          
        <Col className="mobilenav" xs= "auto" >
          <HamburgerButton 
          onClick={this.handleNavClick.bind(this)}/>
        </Col> 
        <Col>
          {this.renderMobileNav()}
        </Col>
        </Row>
        </Container>
      ];
    } else {
      return [
        <div className="navmenu">
          {this.navigationLinks()}
        </div>
      ]; 
    }
  }  

  render() {
    return(
      <div id="sitebody">
        {this.renderNavigation()}
        <div
          style={{
            backgroundColor: "#EAEDED",
            backgroundSize:'cover',
            paddingLeft:'10px',
            paddingTop: '5px'}}
        >
          {this.props.children}
        </div>
        <div id="footer">
          <div>
            <p
              class="footer-block"
              style={{
                bottom: "0",
                textAlign: "right",
                paddingRight: "10px",
                paddingTop: "20px",
                paddingBottom: "20px",
                overflow: "hidden",
                backgroundColor: "#BFC9CA"
              }}
            >
              &copy; 2018 Moloch Ventures
            </p>
          </div>
        </div>
      </div>
    )
  }
}
export default Layout;



