// src/App.jsx
import {BrowserRouter as Router,HashRouter, Route, Switch} from "react-router-dom";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Container,
  Row,
  Col,
  Jumbotron,
  Button
} from "reactstrap";
import React, {Component} from "react";
import "./pages/Home.css";
import ScrollableAnchor from "react-scrollable-anchor";
import { removeHash, configureAnchors } from 'react-scrollable-anchor'
import Layout from "./Layout";
import Home from "./pages/Home";
import Acknowledgments from "./pages/Contributors";
import WhyStateChannels from "./pages/Why-do-we-need-state-channels";
import Statechannels101 from "./pages/state-channels-101";
import Paymentchannels from "./pages/payment-channels-vs-state-channels";
import Counterfactualinstantiation from "./pages/counterfactual-instantiation";
import Htlcs from "./pages/hashed-timelock-contracts";
import Virtualchannels from "./pages/virtual-channels";
import Publications from "./pages/Publications";
import Research from "./pages/Research";
import Projects from "./pages/Projects";
import GettingStarted from "./pages/GettingStarted";
import PaymentChannelContracts from "./pages/PaymentChannelWalkthrough";


configureAnchors({offset: -75, scrollDuration: 500})

  class App extends Component {

    //configure anchors

  componentDidMount() {
      window.addEventListener('scroll', this.handleScroll);
  };

  componentWillUnmount() {
      window.removeEventListener('scroll', this.handleScroll);
  };
  handleScroll(event) {
      removeHash()
  };
  render() {
    return (
      <Layout>
        <Router>
          <Switch>
            <Route exactpath="" component={Home} />
            <Route path="#Acknowledgments" component={Acknowledgments} />
            <Route path="#GettingStarted" component={GettingStarted} />
            <Route path="#Research" component={Research} />
            <Route path="#Publications" component={Publications} />
            <Route path="#Projects" component={Projects} />
            <Route path="#statechannels101" component={Statechannels101} />
            <Route path="#whyStateChannels" component={WhyStateChannels} />
            <Route path="#paymentchannels" component={Paymentchannels} />
            <Route
              path="#counterfactualinstantiation"
              component={Counterfactualinstantiation}
            />
            <Route path="#htlcs" component={Htlcs} />
            <Route path="#virtualchannels" component={Virtualchannels} />
            <Route path="#PaymentChannelContracts" component={PaymentChannelContracts} />

            </Switch>
        </Router>
        <ScrollableAnchor id={'GettingStarted'}>
<div onScroll={this.handleScroll}>        <script>goToAnchor('GettingStarted', true)</script>
        <GettingStarted />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'WhyStateChannels'}>
<div onScroll={this.handleScroll}>        <WhyStateChannels />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Statechannels101'}>
<div onScroll={this.handleScroll}>        <Statechannels101 />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Paymentchannels'}>
<div onScroll={this.handleScroll}>        <Paymentchannels />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Counterfactualinstantiation'}>
<div onScroll={this.handleScroll}>        <Counterfactualinstantiation />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Htlcs'}>
<div onScroll={this.handleScroll}>        <Htlcs />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Virtualchannels'}>
<div onScroll={this.handleScroll}>        <Virtualchannels />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Research'}>
<div onScroll={this.handleScroll}>        <Research />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Publications'}>
<div onScroll={this.handleScroll}>        <Publications />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'Projects'}>
<div onScroll={this.handleScroll}>        <Projects />
        </div>
        </ScrollableAnchor>
        <ScrollableAnchor id={'PaymentChannelContracts'}>
<div onScroll={this.handleScroll}>        <PaymentChannelContracts />
        </div>
        </ScrollableAnchor>
        
        <ScrollableAnchor id={'Acknowledgments'}>
<div onScroll={this.handleScroll}>        <Acknowledgments />
        </div>
        </ScrollableAnchor>

      </Layout>
    );
  }
}

export default App;



